'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';

export function RegisterForm() {
  const router = useRouter();

  const [formData, setFormData] = useState({
    username: '',
    email: '',
    nombre: '',
    apellido: '',
    password: '',
    confirmPassword: '',
  });

  const [errors, setErrors] = useState<Record<string, string>>({});
  const [isLoading, setIsLoading] = useState(false);
  const [success, setSuccess] = useState(false);

  // Mostrar / Ocultar contrase√±a
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirm, setShowConfirm] = useState(false);

  const togglePassword = () => setShowPassword(!showPassword);
  const toggleConfirmPassword = () => setShowConfirm(!showConfirm);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;

    setFormData(prev => ({ ...prev, [name]: value }));

    if (errors[name]) {
      setErrors(prev => {
        const newErrors = { ...prev };
        delete newErrors[name];
        return newErrors;
      });
    }
  };

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.username) newErrors.username = 'El usuario es requerido';
    else if (formData.username.length < 3)
      newErrors.username = 'Debe tener al menos 3 caracteres';

    if (!formData.email) newErrors.email = 'El email es requerido';
    else if (!/\S+@\S+\.\S+/.test(formData.email))
      newErrors.email = 'Email inv√°lido';

    if (!formData.nombre) newErrors.nombre = 'El nombre es requerido';
    if (!formData.apellido) newErrors.apellido = 'El apellido es requerido';

    if (!formData.password) newErrors.password = 'La contrase√±a es requerida';
    else if (formData.password.length < 6)
      newErrors.password = 'Debe tener al menos 6 caracteres';

    if (formData.password !== formData.confirmPassword)
      newErrors.confirmPassword = 'Las contrase√±as no coinciden';

    setErrors(newErrors);

    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (!validateForm()) return;

    setIsLoading(true);

    setTimeout(() => {
      const users = JSON.parse(localStorage.getItem('registered_users') || '[]');

      users.push({
        id: users.length + 1,
        ...formData,
        rol: 'vendedor',
        createdAt: new Date().toISOString(),
      });

      localStorage.setItem('registered_users', JSON.stringify(users));

      setSuccess(true);
      setIsLoading(false);

      setTimeout(() => router.push('/login'), 2000);
    }, 1200);
  };

  // Pantalla de √©xito
  if (success) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-green-50 via-emerald-50 to-teal-50 p-4">
        <div className="bg-white rounded-2xl shadow-2xl p-8 max-w-md w-full text-center scale-105 transition">
          <div className="w-20 h-20 bg-green-100 rounded-full mx-auto mb-4 flex items-center justify-center shadow-lg">
            <svg className="w-12 h-12 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
            </svg>
          </div>

          <h2 className="text-2xl font-bold text-gray-900 mb-2">
            ¬°Cuenta Creada!
          </h2>

          <p className="text-gray-600 mb-6">
            Redirigiendo al inicio de sesi√≥n...
          </p>

          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-green-600 mx-auto"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-purple-50 via-pink-50 to-rose-50 p-4">
      <div className="w-full max-w-2xl">
        <div className="bg-white rounded-2xl shadow-2xl overflow-hidden">

          {/* ENCABEZADO */}
          <div className="bg-gradient-to-r from-purple-600 to-pink-600 p-8 text-center text-white">
            <div className="w-20 h-20 bg-white rounded-full mx-auto mb-4 flex items-center justify-center shadow-xl">
              <svg className="w-12 h-12 text-purple-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M18 9v3m0 0v3m0-3h3m-3 0h-3m-2-5a4 4 0 11-8 0 4 4 0 018 0zM3 20a6 6 0 0112 0v1H3v-1z" />
              </svg>
            </div>

            <h1 className="text-3xl font-bold">Crear Cuenta</h1>
            <p className="text-purple-100 text-sm mt-1">
              √önete al Sistema POS
            </p>
          </div>

          {/* FORMULARIO */}
          <div className="p-8">
            <form onSubmit={handleSubmit} className="space-y-6">

              {/* Primera fila */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
                {/* Usuario */}
                <div>
                  <label className="text-sm font-semibold text-gray-700">
                    Usuario *
                  </label>
                  <input
                    type="text"
                    name="username"
                    value={formData.username}
                    onChange={handleChange}
                    placeholder="usuario123"
                    className={`w-full px-4 py-3 mt-1 border rounded-lg shadow-sm focus:ring-2 focus:ring-purple-500 ${
                      errors.username ? 'border-red-500' : 'border-gray-300'
                    }`}
                  />
                  {errors.username && (
                    <p className="text-red-600 text-sm mt-1">{errors.username}</p>
                  )}
                </div>

                {/* Email */}
                <div>
                  <label className="text-sm font-semibold text-gray-700">
                    Email *
                  </label>
                  <input
                    type="email"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    placeholder="correo@ejemplo.com"
                    className={`w-full px-4 py-3 mt-1 border rounded-lg shadow-sm focus:ring-2 focus:ring-purple-500 ${
                      errors.email ? 'border-red-500' : 'border-gray-300'
                    }`}
                  />
                  {errors.email && (
                    <p className="text-red-600 text-sm mt-1">{errors.email}</p>
                  )}
                </div>
              </div>

              {/* Segunda fila */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
                {/* Nombre */}
                <div>
                  <label className="text-sm font-semibold text-gray-700">
                    Nombre *
                  </label>
                  <input
                    name="nombre"
                    value={formData.nombre}
                    onChange={handleChange}
                    placeholder="Juan"
                    className={`w-full px-4 py-3 mt-1 border rounded-lg shadow-sm focus:ring-2 focus:ring-purple-500 ${
                      errors.nombre ? 'border-red-500' : 'border-gray-300'
                    }`}
                  />
                  {errors.nombre && (
                    <p className="text-red-600 text-sm mt-1">{errors.nombre}</p>
                  )}
                </div>

                {/* Apellido */}
                <div>
                  <label className="text-sm font-semibold text-gray-700">
                    Apellido *
                  </label>
                  <input
                    name="apellido"
                    value={formData.apellido}
                    onChange={handleChange}
                    placeholder="P√©rez"
                    className={`w-full px-4 py-3 mt-1 border rounded-lg shadow-sm focus:ring-2 focus:ring-purple-500 ${
                      errors.apellido ? 'border-red-500' : 'border-gray-300'
                    }`}
                  />
                  {errors.apellido && (
                    <p className="text-red-600 text-sm mt-1">{errors.apellido}</p>
                  )}
                </div>
              </div>

              {/* CONTRASE√ëAS */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-5">

                {/* Password */}
                <div className="relative">
                  <label className="text-sm font-semibold text-gray-700">
                    Contrase√±a *
                  </label>

                  <input
                    type={showPassword ? 'text' : 'password'}
                    name="password"
                    value={formData.password}
                    onChange={handleChange}
                    placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                    className={`w-full px-4 py-3 mt-1 border rounded-lg shadow-sm focus:ring-2 focus:ring-purple-500 ${
                      errors.password ? 'border-red-500' : 'border-gray-300'
                    }`}
                  />

                  {/* Icono */}
                  <button
                    type="button"
                    onClick={togglePassword}
                    className="absolute right-3 top-10 text-gray-500 hover:text-gray-800"
                  >
                    {showPassword ? 'üôà' : 'üëÅÔ∏è'}
                  </button>

                  {errors.password && (
                    <p className="text-red-600 text-sm mt-1">{errors.password}</p>
                  )}
                </div>

                {/* Confirm password */}
                <div className="relative">
                  <label className="text-sm font-semibold text-gray-700">
                    Confirmar Contrase√±a *
                  </label>

                  <input
                    type={showConfirm ? 'text' : 'password'}
                    name="confirmPassword"
                    value={formData.confirmPassword}
                    onChange={handleChange}
                    placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                    className={`w-full px-4 py-3 mt-1 border rounded-lg shadow-sm focus:ring-2 focus:ring-purple-500 ${
                      errors.confirmPassword ? 'border-red-500' : 'border-gray-300'
                    }`}
                  />

                  <button
                    type="button"
                    onClick={toggleConfirmPassword}
                    className="absolute right-3 top-10 text-gray-500 hover:text-gray-800"
                  >
                    {showConfirm ? 'üôà' : 'üëÅÔ∏è'}
                  </button>

                  {errors.confirmPassword && (
                    <p className="text-red-600 text-sm mt-1">{errors.confirmPassword}</p>
                  )}
                </div>
              </div>

              {/* T√©rminos */}
              <div className="flex items-start">
                <input type="checkbox" className="mt-1 w-4 h-4 text-purple-600" required />
                <p className="ml-2 text-sm text-gray-600">
                  Acepto los{' '}
                  <a className="text-purple-600 font-semibold hover:underline">t√©rminos</a>{' '}
                  y{' '}
                  <a className="text-purple-600 font-semibold hover:underline">
                    la privacidad
                  </a>.
                </p>
              </div>

              {/* Bot√≥n */}
              <button
                type="submit"
                disabled={isLoading}
                className="w-full bg-gradient-to-r from-purple-600 to-pink-600 text-white py-3.5 rounded-lg font-semibold shadow-lg hover:shadow-xl hover:from-purple-700 hover:to-pink-700 transition-all disabled:opacity-50 flex items-center justify-center"
              >
                {isLoading ? (
                  <>
                    <svg className="animate-spin mr-3 h-5 w-5" viewBox="0 0 24 24">
                      <circle className="opacity-25" cx="12" cy="12" r="10" stroke="white" strokeWidth="4" />
                      <path className="opacity-75" fill="white" d="M4 12a8 8 0 018-8V0C5 0 0 5 0 12h4z"></path>
                    </svg>
                    Creando...
                  </>
                ) : (
                  <>Crear Cuenta</>
                )}
              </button>
            </form>
          </div>

          {/* FOOTER */}
          <div className="bg-gray-50 p-5 border-t text-center text-sm text-gray-600">
            ¬øYa tienes una cuenta?{' '}
            <Link href="/login" className="text-purple-600 hover:underline font-semibold">
              Iniciar sesi√≥n
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}
